	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/simple-box2d-better-cleverer-more-optimised/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/box2d-shapes-2-e52dcb46a0a91c49c1d0d8427cce3475-e8574.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"box2d shapes 2\"\n        title=\"\"\n        src=\"/portfolio-2018/static/box2d-shapes-2-e52dcb46a0a91c49c1d0d8427cce3475-e8574.jpg\"\n        srcset=\"/portfolio-2018/static/box2d-shapes-2-e52dcb46a0a91c49c1d0d8427cce3475-c94d9.jpg 158w,\n/portfolio-2018/static/box2d-shapes-2-e52dcb46a0a91c49c1d0d8427cce3475-9885b.jpg 315w,\n/portfolio-2018/static/box2d-shapes-2-e52dcb46a0a91c49c1d0d8427cce3475-e8574.jpg 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><em>^ This time, that's an image - saves on the page rendering time!</em></p>\n<p>Another update on the Box2D classes I'm working on.  No new features, but some pretty cool expansions and improvements to the library object creation.</p>\n<p>If you check out <a href=\"http://www.psyked.co.uk/box2d/demo-source-simple-box2d-with-curved-edges.htm\">the previous post</a>, you'll see that the the dynamically drawn shapes weren't exactly perfect; nor were they very efficient.  Oh they worked fine, and traced the detail of the shape very well - but it took a decent chunk of system resources to animate and ended up with some overlaid shapes; which all made the Box2D a bit sluggish and the behaviours buggy.</p>\n<p>So, stealing incorporating ideas from other projects has been the name of the game - namely the ear clipping optimisation code from <a href=\"http://www.splashdust.net/2009/10/box2d-mouse-drawing-now-with-ear-clipping/\">Splashdust.net</a>, combined with some of my own cobbled-together more optimised code for tracing curved edges.</p>\n<h3>Live demo:</h3>\n<p>Mouse interaction is enabled in the below example - click and drag the objects to check out how it works.</p>\n<p>[kml_flashembed movie=\"<a href=\"http://uploads.psyked.co.uk/2010/02/LettersExperiment.swf\">http://uploads.psyked.co.uk/2010/02/LettersExperiment.swf</a>\" height=\"280\" width=\"500\" /]</p>\n<h3><a href=\"http://uploads.psyked.co.uk/2010/02/simplebox2d_demo_100202.zip\">Download the source code for this demo.</a></h3>\n<p>* The source code isn't exactly cleaned up - sometime, eventually, it will be.</p>","frontmatter":{"title":"Simple Box2D - Better, cleverer, more optimised.","date":"February 03, 2010","tags":["box2d","actionscript"],"url":null}}},"pathContext":{}}

